.progressBarContainer {
    width: 100%;
    height: 8px;
    border-radius: 10px;
    background-color: #344767 !important;
    position: relative;
  }
  
  .progressBarFill {
    height: 100%;
    background-color: #fb8c00;
    border-radius: 10px;
    transition: width 0.3s ease-in-out;
    position: relative;
    overflow: hidden;
  }
  
  .progress-bar-label {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    font-size: 12px;
    color: #000 !important;
  }
  